<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process149"></a>

<p><br></p>
<p><span class="rvts17">章节探索</span><span class="rvts50"> </span><span class="rvts17">浮木城</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:508,Y:47</span><span class="rvts21">，地点：岸边</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">探索新的地图</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1094.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">在进入死神海岸（</span><span class="rvts20">Reaper's Coast</span><span class="rvts21">）后，由于地图较大，地图上的危险较多。因此有必要进行细致的路线安排计划。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1095.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">按照常规的思路，首先接触到的是浮木城（坐标</span><span class="rvts20">X:344,Y:116</span><span class="rvts21">）。在浮木城中，</span><span class="rvts20">NPC</span><span class="rvts21">较多，商人也很多，支线任务也较多。因此是一个可以提升等级，装备和金钱的好地方。</span>
<br><span class="rvts21">浮木城看似很大，其实则不然，但有一点是肯定的，人特别多。不同与欢乐堡和阿玛蒂亚避难所，在浮木城里，无论是官是民，素质都较差（大多数人充满了谎言，欺诈和奸猾）。</span>
<br><span class="rvts21">浮木城的广场有商人，可以买到很多有用的物资。浮木城的码头有矮人在做渔业，任务较少。另外，也可以留意浮木城的周边区域，也有一些可以接到的小任务。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1096.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">浮木城有一所比较大的酒馆，酒馆有额外的上层和地下层内容。上层主要是休息间，里面充满了高贵的旅客和官差（有个富商，出售各种史诗武器防具）。</span>
<br><span class="rvts21">酒馆下层是一个地下竞技场，里面有矮人黑帮团伙，以及一个旅人休息的区域。（还有一处秘密的麻叶种植屋）</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1097.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">失事洞穴（</span><span class="rvts20">The Wrecker's Cave</span><span class="rvts21">）位于浮木城西边，这里面有支线</span><span class="rvts20"></span><span class="rvts21">浮木阴影</span><span class="rvts20"></span><span class="rvts21">，</span><span class="rvts20"></span><span class="rvts21">教会的法律</span><span class="rvts20"></span><span class="rvts21">，</span><span class="rvts20"></span><span class="rvts21">自由的味道</span><span class="rvts20"></span><span class="rvts21">以及</span><span class="rvts20"></span><span class="rvts21">源力觉醒</span><span class="rvts20"></span><span class="rvts21">支线任务的重要结点。</span>
<br><span class="rvts21">如果需要这些支线任务的达成条件，请参考下篇的失事洞穴探索篇。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



